.kweet {
    margin-bottom: 20px;
    background-color: rgb(243, 240, 240);
    max-width: 320px;
    padding: 20px;
    border-radius: 12px;
    position: relative; 
    display: flex;
    flex-direction: column;
    color: rgba(0, 0, 0, 0.8);
}

.btns{
    line-height:47.2px;
}

.delete__btn,.edit__btn{
    color:rgb(152, 152, 152);
    border:none;
    background:none;
    cursor:pointer;
    font-size:12px;
    padding:0 3px;
}

.update__box{
    position:absolute;
    right:8px;
    bottom:4px;
    font-size:12px;
    cursor:pointer;
    color:rgb(125, 132, 129);
}

.form__btn{
    margin-right:7px;
}

.title__box{
    width:280px;
}

.kweet__title{
    word-break:break-all;
    margin:23px 0;
}

.kweet__textarea{
    width:100%;
    height:100px;
    resize:none;
    background-color:transparent;
    border:none;
    font-family: 'un Batang', serif;
    font-family: 'Nunito', sans-serif;
}

.kweet__textarea:focus{
    background-color:transparent;
    outline:none;
    
}

.bottom__box{
    display:flex;
    justify-content: space-between;
    text-align: center;
}

.user__nickname{
    font-size:12px;
    color:gray;
    margin:0;
    line-height:47.2px;
}