
.factoryForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-top:10px;
}

.factoryInput__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 10px;
    width: 100%;
}

.factoryInput__input {
    flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    border: 1px solid #c6c6c0;
    border-radius: 20px;
    font-weight: 500;
    font-size: 12px;
}

.choose__file{
    cursor:pointer;
    color:#92b292;
    padding-left:9px;
}

.submit__btn{
    color:#7aa57d;
    background-color:transparent;
    border:none;
    cursor:pointer;
    margin-left:3px;
}


.preview{
    display: flex;
    justify-content: space-between;
    padding:5px 20px;
}

.preview__text{
    color:#3d3d3d;
    font-size:12px;
    text-align: right;
    
}

#image__upload,#text__upload{
    transition:all 0.2s ease;
}

#image__upload:hover{
    color:rgb(86, 100, 46);
}

#text__upload:hover{
    color:rgb(86, 100, 46);

    
}

#undo{
    color:#515151;
    cursor: pointer;
}